.navbar {
  background-color: rgb(11, 59, 194);
  margin: 0;
  padding: 0;
  color: white;
}

.navbar ul {
  width: 100%;
  margin: 0;
  padding: 0 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.navbar ul li {
  list-style: none;
  width: 100%;
}

.title {
  text-align: start;
}
.guess {
  text-align: center;
  -webkit-transition: 0.3 all ease-In;
  transition: 0.3 all ease-In
}

.score {
  text-align: end;
}
.jumbotron {
  width: 100%;
  background-color: #777;
  background: url(/reactClickyGame/static/media/weights.06b2e5f6.jpeg);
  background-size: cover;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  height: 500px;
  z-index: -2;

}

.jumbotron h1 {
  font-size: 4rem;
  margin: 0;
}

.jumbotron h3 {
  font-size: 1.75rem;
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 225px;
  height: 225px;
  margin: 10px;
  background: #333;
  border-radius: 5px;
  cursor: pointer;
}

.card img {
  display:-webkit-flex;
  display:flex;
  width: 215px;
  height: 215px;
}


* {
  box-sizing: border-box;
}

html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}

body {
  background-color: rgba(67, 67, 67, 0.9)
}

.cards {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
